/**
 * 要素の垂直位置を上寄せ。
 */
.l-Grid-top {
  align-items: flex-start;
}
@include mq-up(md) {
  .l-Grid-topMd {
    align-items: flex-start;
  }
}
@include mq-up(lg) {
  .l-Grid-topLg {
    align-items: flex-start;
  }
}
@include mq-up(xl) {
  .l-Grid-topXl {
    align-items: flex-start;
  }
}

/**
 * 要素の垂直位置を真ん中寄せ。
 */
